e-height: 20px;\r\n    padding: 1px 5px;\r\n    white-space: nowrap;\r\n    opacity: 0;\r\n    transform: translateX(-50%);\r\n    position: absolute;\r\n    left: 50%;\r\n    top: -30px;\r\n}\r\n.product-grid .social li a:after {\r\n    content: '';\r\n    height: 15px;\r\n    width: 15px;\r\n    border-radius: 0;\r\n    transform: translateX(-50%) rotate(45deg);\r\n    top: -20px;\r\n    z-index: -1;\r\n}\r\n.product-grid .social li a:hover:after, .product-grid .social li a:hover:before {\r\n    opacity: 1;\r\n}\r\n.product-grid .product-discount-label, .product-grid .product-new-label {\r\n    color: #fff;\r\n    background-color: #ef5777;\r\n    font-size: 12px;\r\n    text-transform: uppercase;\r\n    padding: 2px 7px;\r\n    display: block;\r\n    position: absolute;\r\n    top: 10px;\r\n    left: 0;\r\n}\r\n.product-grid .product-discount-label {\r\n    background-color: #333;\r\n    left: auto;\r\n    right: 0;\r\n}\r\n.product-label\r\n{\r\n    font-size: 14px !important;\r\n}\r\n.product-grid .rating {\r\n    color: #ffd200;\r\n    font-size: 12px;\r\n    padding: 5px 0 0;\r\n    margin: 0;\r\n    list-style: none;\r\n    position: relative;\r\n    z-index: -1;\r\n}\r\n.product-grid .rating li.disable {\r\n    color: rgba(0, 0, 0, 0.2);\r\n}\r\n.product-grid .product-content {\r\n    background-color: #fff;\r\n    text-align: center;\r\n    padding: 10px 0;\r\n    margin: 0 auto;\r\n    position: absolute;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: -28px;\r\n    z-index: 1;\r\n    transition: all 0.3s;\r\n}\r\n.product-grid:hover .product-content {\r\n    bottom: 0;\r\n}\r\n.product-grid .title {\r\n    font-size: 13px;\r\n    font-weight: 600;\r\n    letter-spacing: 0.5px;\r\n    text-transform: capitalize;\r\n    transition: all 0.3s ease 0s;\r\n}\r\n.product-grid .title a {\r\n    color: #828282;\r\n}\r\n.product-grid .title a:hover {\r\n    color: #ef5777;\r\n}\r\n.product-grid:hover .title a {\r\n    color: #ef5777;\r\n}\r\n.product-grid .price {\r\n    color: #333;\r\n    font-size: 17px;\r\n    font-family: Montserrat, sans-serif;\r\n    font-weight: 700;\r\n    letter-spacing: 0.6px;\r\n    margin-bottom: 8px;\r\n    text-align: center;\r\n    transition: all 0.3s;\r\n}\r\n.product-grid .price span {\r\n    color: #999;\r\n    font-size: 13px;\r\n    font-weight: 400;\r\n    text-decoration: line-through;\r\n    margin-left: 3px;\r\n    display: inline-block;\r\n}\r\n.product-grid .add-to-cart {\r\n    color: #000;\r\n    font-size: 13px;\r\n    font-weight: 600;\r\n}\r\n@media only screen and (max-width: 990px) {\r\n    .product-grid {\r\n        margin-bottom: 30px;\r\n   }\r\n}\r\n.no-rightpad\r\n{\r\n    padding-right: 0 !important;\r\n}\r\n.no-leftpad\r\n{\r\n    padding-left: 0 !important;\r\n}\r\n.no-pad\r\n{\r\n    padding-right: 0 !important;\r\n    padding-left: 0 !important;\r\n}\r\n.productright\r\n{\r\n    padding-right: 45px;\r\n}\r\n.productleft\r\n{\r\n    border-right:1px solid #bebaba;\r\n    background-color: #fff;\r\n}\r\n@media only screen and (max-width: 767px) {\r\n    .productright\r\n    {\r\n        padding-right: 45px;\r\n        padding-left: 30px;\r\n    }  \r\n}\r\n#productfilter {\r\n    /* Remove default list styling */\r\n    list-style-type: none;\r\n    padding: 0;\r\n    margin: 0;\r\n    height: 100%;\r\n    /* max-height: 240px;\r\n    overflow-y: auto; */\r\n}\r\n#productfilter li a {\r\n    background-color: #fff; \r\n    padding: 3px 12px;\r\n    text-decoration: none; \r\n    font-size: 14px; \r\n    color: black; \r\n    display: block;\r\n}\r\n#productfilter li a.header {\r\n    padding: 3px 12px;  \r\n    font-weight: bold;\r\n    cursor: default; /* Change cursor style */\r\n}\r\n#productfilter li a:hover:not(.header) {\r\n    background-color: #3BAFDA; /* Add a hover effect to all links, except for headers */\r\n    color: #fff;\r\n}\r\n#productfilter li input {\r\n    cursor: pointer; \r\n}\r\n#productfilter.notFound::after {\r\n  padding: 0px 6px;  \r\n  c